
.main {
    max-width: 1080px;
    margin: 0 auto;
    padding: 3em;
}

@media(max-width: 768px) {
    .main {
        padding: 2.4em;
    }
}

@media(max-width: 630px) {
    .main {
        padding: 2em;
    }
}

@media(max-width: 400px) {
    .main {
        padding: 1.5em;
    }
}